import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { addAction } from '../../store/actions'
import Banner from '../../components/Banner/index'
import InfiniteScroll from 'react-infinite-scroll-component'
import { Layout, Button, Comment, List, Typography, BackTop, Spin, Icon } from 'antd'
import moment from 'moment'
import './index.css'
const { Header, Content } = Layout
const { Paragraph } = Typography

class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      hasMore: true,
      list: [],
      page: 1,
      size: 10
    }
  }

  componentDidMount() {
    const { page, size } = this.state
    this.getList(page, size)
  }

  getList = (page = 1, size = 10) => {
    this.post('/api/moodList', {
      page,
      size
    }).then(res => {
      if (res.length === 0) {
        this.setState({ hasMore: false })
        return
      }

      this.setState((state, props) => ({
        list: [...state.list, ...res]
      }))
    })
  }

  next = () => {
    this.setState(
      (state, props) => ({
        page: state.page + 1
      }),
      () => {
        this.getList(this.state.page, this.state.size)
      }
    )
  }

  mapdata = (arr = []) => {
    return arr.map(item => ({
      author: item.name || '他不想有名字',
      avatar: item.avatar ? this.baseUrl+item.avatar : (<Icon type="meh" theme="twoTone" style={{ fontSize: '32px' }} />) ,
      content: (
        <Paragraph ellipsis={{ rows: 2, expandable: true }}>
          {item.article}
        </Paragraph>
      ),
      datetime: (
        <span>
          { item.publish_date ? moment(item.publish_date).format('YYYY年MM月DD日 HH:mm:ss') : '...'}
        </span>
      )
    }))
  }

  render() {
    const data = this.mapdata(this.state.list)

    return (
      <InfiniteScroll
        dataLength={data.length} //This is important field to render the next data
        next={this.next}
        hasMore={this.state.hasMore}
        loader={
          <div className='loadding-icon'>
            <Spin />
          </div>
        }
        endMessage={
          <p className='bottom-tip'>
            <b>没有了哦~~~</b>
          </p>
        }>
        <Layout>
          <Header>
            <div className='logo'>生活情绪</div>
          </Header>
          <Banner />
          <Content>
            <div className='publishBtn'>
              <Button>
                <Link to='/publish'>发表心情</Link>
              </Button>
            </div>
            <List
              className='comment-list'
              header={`共 ${data.length} 条`}
              itemLayout='horizontal'
              dataSource={data}
              renderItem={item => (
                <li>
                  <Comment
                    actions={item.actions}
                    author={item.author}
                    avatar={item.avatar}
                    content={item.content}
                    datetime={item.datetime}
                  />
                </li>
              )}
            />
          </Content>
        </Layout>
        <BackTop />
      </InfiniteScroll>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    num: state.home.a
  }
}

const mapDispatchToProps = {
  addAction
}

export default connect(mapStateToProps, mapDispatchToProps)(Home)
