import React, { Component } from 'react'
import { Carousel } from 'antd'
import './banner.css'
class Banner extends Component {
  render() {
    const bgGround = {
      background: `url(${require('../../image/banner/b1.jpg')})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center 0'
    }

    const bgGround2 = {
      background: `url(${require('../../image/banner/b2.jpg')})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center 0'
    }

    return (
      <div>
        <Carousel autoplay>
          <div>
            <div style={bgGround} className="item">
              <h3>如果你找不到一个可以说话的人, 或许可以来这里写下心情</h3>
            </div>
          </div>

          <div>
            <div style={bgGround2} className="item">
              <h3>你甚至不必留下任何信息, 你也可以看到别人的心情</h3>
            </div>
          </div>
        </Carousel>
      </div>
    )
  }
}
export default Banner
