import axios from "axios";
import { Component } from "react";

// let base = "http://localhost:7001";
let base = "";

// create an axios instance
const service = axios.create({
  withCredentials: true, // send cookies when cross-domain requests
  timeout: 10000 // request timeout
})

// 请求前拦截
service.interceptors.request.use(
  config => {
    return config;
  },
  err => {
    console.log("请求超时");
    return Promise.reject(err);
  }
);

// 返回后拦截
service.interceptors.response.use(
  response => {
    if(response.status === 200 ) {
      if(response.data.state === 'success') {
        return response.data.data;
      } else {
        console.log('请求出错')
      }
    } else {
      console.log('服务器出错')
    }
  },
  err => {
    return Promise.reject(err);
  }
);

// @RequestBody请求
const post = (url, params) => {
  return service({
    method: "post",
    url: `${base}${url}`,
    data: params,
    headers: {
      "Content-Type": "application/json",
      charset: "utf-8"
    }
  });
};

// @RequsetParam请求
const postRequestParam = (url, params) => {
  return service({
    method: "post",
    url: `${base}${url}`,
    data: params,
    transformRequest: [
      function(data) {
        let ret = "";
        for (let it in data) {
          ret +=
            encodeURIComponent(it) + "=" + encodeURIComponent(data[it]) + "&";
        }
        return ret;
      }
    ],
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    }
  });
};

const get = url => {
  return service({
    method: "get",
    url: `${base}${url}`
  });
};

const multiple = function(requsetArray, callback) {
  service.all(requsetArray).then(axios.spread(callback));
};

Component.prototype.baseUrl = base;
Component.prototype.get = get;
Component.prototype.post = post;
Component.prototype.postRequestParam = postRequestParam;
Component.prototype.multiple = multiple;

export default {
  post,
  get,
  postRequestParam,
  multiple
}