import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Form, Button, Input, Layout } from 'antd'
import './publish.css'

import Avatar from '../../components/Avatar/index'
const { Header } = Layout

class PublishForm extends Component {
  handleReset = e => {
    e.preventDefault()
    this.props.form.resetFields()

    this.props.history.push('/')
  }

  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFields(async (errors, values) => {
      if (!errors) {
        const res = await this.post('/api/addMood', {
          avatar: this.props.src,
          name: values.name || '他不想有名字',
          article: values.article,
          publish_date: new Date()
        })

        if(res){
          this.props.history.push('/')
        }
      }
     
    })
  }


  render() {
    const { getFieldDecorator } = this.props.form

    return (
      <div>
        <Header>
          <div className='logo'>生活情绪</div>
        </Header>
        <Form className='publish-form' layout='horizontal'>
          <Form.Item label='随便起个名字吧' labelCol={{ span: 6 }} wrapperCol={{ span: 16 }}>
            {
              getFieldDecorator('name', {})(
                <Input />
              )
            }
          </Form.Item>

          <Form.Item label='传一个头像' labelCol={{ span: 6 }} wrapperCol={{ span: 16 }}>
            {
              <Avatar />
            }
          </Form.Item>

          <Form.Item label='写下想说的话' labelCol={{ span: 6 }} wrapperCol={{ span: 16 }}>
            {
              getFieldDecorator('article', {
                rules: [{ required: true, message: '至少要写一句话吧' }]
              })(
                <Input.TextArea></Input.TextArea>
              )
            }
          </Form.Item>
          <Form.Item wrapperCol={{ span: 14, offset: 6 }}>
            <Button type='primary' onClick={this.handleSubmit}>
              发表心情
            </Button>
            &nbsp;&nbsp;&nbsp;
            <Button onClick={this.handleReset}>算了</Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

const Publish = Form.create()(PublishForm)

const mapStateToProps = (state, ownProps) => {
  return {
    src: state.avatar.src
  }
}

const mapDispatchToProps = {
}

export default connect(mapStateToProps, mapDispatchToProps)(Publish);
